.login-container {
    background: #1a1a1a;
    color: white;
    width: 350px;
    margin: 10% auto;
    padding: 2rem;
    border-radius: 12px;
    text-align: center;
  }
  input {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    background: #2b2b2b;
    border: none;
    color: white;
  }
  button {
    width: 100%;
    padding: 10px;
    margin-top: 10px;
    border: none;
    color: white;
    background: #c084fc;
    border-radius: 5px;
  }
  .guest {
    background: transparent;
    border: 1px solid cyan;
    margin-top: 10px;
  }
  