.search-container {
    margin: 20px 0;
  }
  
  .search-container input {
    padding: 10px;
    width: 200px;
    border-radius: 5px;
    margin-right: 10px;
    border: none;
  }
  
  .search-container button {
    padding: 10px 20px;
    background-color: #4e4cf0;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .error {
    color: red;
    margin-top: 10px;
  }
  